import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {
    path: '/', redirect: '/index'
  },


  //管护大数据
  { path: '/ghdsj8', name: 'ghdsj8', meta:{title:'检测设备'},component: () => import('@/components/DaShuJu/GuanHuShu.vue') },
  { path: '/zaoxing', name: 'zaoxing', meta:{title:'造型设备'}, component: () => import('@/components/DaShuJu/ZaoXingSheBei.vue') },
  { path: '/ronghlian', name: 'ronghlian', meta:{title:'熔炼设备'}, component: () => import('@/components/DaShuJu/RongLian.vue') },
  { path: '/zhouzao', name: 'zhouzao', meta:{title:'铸造设备'}, component: () => import('@/components/DaShuJu/ZhouZao.vue') },
  { path: '/index', name: 'index', meta:{title:'铸造厂数据可视化系统'}, component: () => import('@/components/DaShuJu/Index.vue') },




  { path: '*', name: 'notFound', component: () => import('@/components/NotFound.vue') },
]

const router = new VueRouter({
  base: "process.env.BASE_URL",
  routes
})


