import Vue from 'vue'
import App from './App.vue'

import echarts from 'echarts'
import './plugins/element.js'
import '@/assets/css/glober.css'

import { Message } from 'element-ui'

// import dataV from "datav"
import dataV from '@jiaminghi/data-view'

import router from './router'

//引入字体图标库
// import '@/assets/cssicon/iconfont.css'

//引入富文本编辑器
import VueQuillEditor from 'vue-quill-editor'

// 导入富文本编辑器对应的样式
import 'quill/dist/quill.core.css' // import styles
import 'quill/dist/quill.snow.css' // for snow theme
import 'quill/dist/quill.bubble.css' // for bubble theme

//引入axios模块
import axios from 'axios'

Vue.prototype.$axios = axios

//全局时间过滤器
Vue.filter("dateFormat", (date) => {
  const dt = new Date(date);
  const y = dt.getFullYear()
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  const d = (dt.getDate() + '').padStart(2, '0')
  const hh = (dt.getHours() + '').padStart(2, '0')
  const mm = (dt.getMinutes() + '').padStart(2, '0')
  const ss = (dt.getSeconds() + '').padStart(2, '0')
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})

//双击图片新窗口打开的全局方法
Vue.prototype.bigImg = (url) => {
  window.open(url);
}

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

//获取政府所有的角色信息
Vue.prototype.getRoleZF = async () => {
  const { data: res } = await axios.get("/api/roles/select", {
    params: {
      pageNumber: 1,
      pageSize: 100
    }
  })
  if (res.status != "0") {
    Message.closeAll()
    Message.error(res.message)
    return
  } else {
    return res.message

  }
}

//获取管护所有的角色信息
Vue.prototype.getRoleGH = async () => {
  const { data: res } = await axios.get("/api/roles/selectGH", {
    params: {
      pageNumber: 1,
      pageSize: 100,
      type: window.sessionStorage.getItem("ghId")

    }
  })
  if (res.status != "0") {
    Message.closeAll()
    Message.error(res.message)
    return
  } else {
    return res.message

  }
}

